import "./rightbar.css"
import {Users} from "../../dummyData"
import Online from "../online/Online";

export default function Rightbar({profile}) {

    const HomeRightbar = () => {
        return (
            <>
            <div className="birthdayContainer">
                    <img className="birthdayImg" src="assets/gift.png" alt="" />
                    <span className="birthdayText"><b>Kaloqn Angelov</b> and <b>3 other friends</b> have birthday today.</span>
                </div>
                <img className="rightbarAd" src="assets/ad.png" alt="" />
                <h4 className="rightbarTitle">Online Friends</h4>
                <ul className="rightbarFriendList">
                   {Users.map(u=>(
                       <Online key={u.id} user={u} />
                   ))}
                </ul>
            </>
        );
    };

    const ProileRightbar = () => {
        return (
            <>
                <h4 className="rightbarTitle">User Information Title</h4>
                <div className="rightbarInfo">
                    <div className="rightbarInfoItem">
                        <div className="rightbarInfoKey">City:</div>
                        <div className="rightbarInfoValue">New York</div>
                    </div>
                    <div className="rightbarInfoItem">
                        <div className="rightbarInfoKey">From:</div>
                        <div className="rightbarInfoValue">Bulgaria</div>
                    </div>
                    <div className="rightbarInfoItem">
                        <div className="rightbarInfoKey">Relationship:</div>
                        <div className="rightbarInfoValue">Single</div>
                    </div>
                </div>
                <h4 className="rightbarTitle">User Friends</h4>
                <div className="rightbarFollowings">
                    <div className="rightbarFollowing">
                        <img src="assets/person/1.jpeg" alt="" className="rightbarFollowingImg" />
                        <span className="rightbarFollowingName">John Don</span>
                    </div>
                    <div className="rightbarFollowing">
                        <img src="assets/person/2.jpeg" alt="" className="rightbarFollowingImg" />
                        <span className="rightbarFollowingName">John Don</span>
                    </div>
                    <div className="rightbarFollowing">
                        <img src="assets/person/3.jpeg" alt="" className="rightbarFollowingImg" />
                        <span className="rightbarFollowingName">John Don</span>
                    </div>
                    <div className="rightbarFollowing">
                        <img src="assets/person/4.jpeg" alt="" className="rightbarFollowingImg" />
                        <span className="rightbarFollowingName">John Don</span>
                    </div>
                    <div className="rightbarFollowing">
                        <img src="assets/person/5.jpeg" alt="" className="rightbarFollowingImg" />
                        <span className="rightbarFollowingName">John Don</span>
                    </div>
                    <div className="rightbarFollowing">
                        <img src="assets/person/6.jpeg" alt="" className="rightbarFollowingImg" />
                        <span className="rightbarFollowingName">John Don</span>
                    </div>
                </div>
            </>
        )
    }
    return (
        <div className="rightbar">
            <div className="rightbarWrapper">
                {profile ? <ProileRightbar /> : <HomeRightbar />}
            </div>
        </div>
    );
}
